import React from 'react';
import Home from './pages/Home';
import Lesson from './pages/Lesson';
import { Route, Switch } from 'react-router-dom';

function Routes() {
  return (
    <Switch>
      <Route exact path="/dyslexia-frontend/" component={Home} />
      <Route path="/dyslexia-frontend/lesson/:moduleId/:lessonId" component={Lesson} />
    </Switch>
  );
}

export default Routes;